module.exports = [{"file":"README.md","lang":"md","section":"default","code":"<p>By default, the model instance and the form are different entities, the\nmodel is not bound to the form in any way.</p>\n<p>This is usually the preferred setup, it allows easy cancellation and\nprevents hard to find bugs, especially when working with nested forms.</p>\n<p>To sync the model and form (merge the form value into the model) we need\nto invoke a <em>commit</em>  </p>\n<pre class=\"lang-ts\"><code class=\"lang-ts\"><span class=\"hljs-comment\">// dynamicForm: DynamicFormComponent</span>\n\ndynamicForm.tdmForm.commitToModel()\n</code></pre>\n<div class=\"info\">\n  <p><code>commitToModel</code> accepts an optional boolean parameter <code>onlyIfDirty</code>.</p>\n  When set to <code>true</code> it will commit only if the form is dirty.\n</div>\n\n<p>After the commit the model and form are now synced.</p>\n"},{"file":"commit.component.ts","lang":"ts","section":"default","code":"<span class=\"hljs-keyword\">import</span> { Component } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@angular/core'</span>;\n<span class=\"hljs-keyword\">import</span> { Hero } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'../4-element-metadata'</span>;\n\n<span class=\"hljs-meta\">@Component</span>({\n  selector: <span class=\"hljs-string\">'form-commit'</span>,\n  templateUrl: <span class=\"hljs-string\">'./commit.component.html'</span>,\n  styleUrls: [ <span class=\"hljs-string\">'./commit.component.scss'</span> ],\n})\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">class</span> CommitComponent {\n  model = <span class=\"hljs-keyword\">new</span> Hero();\n}\n","title":"Component"},{"file":"commit.component.html","lang":"html","section":"tdmDemo","code":"  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">dynamic-form</span> #<span class=\"hljs-attr\">dynForm</span> [<span class=\"hljs-attr\">model</span>]=<span class=\"hljs-string\">\"model\"</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span> <span class=\"hljs-attr\">mat-raised-button</span> <span class=\"hljs-attr\">color</span>=<span class=\"hljs-string\">\"primary\"</span>\n            [<span class=\"hljs-attr\">disabled</span>]=<span class=\"hljs-string\">\"dynForm.form.status !== 'VALID' || !dynForm.form.dirty\"</span>\n            (<span class=\"hljs-attr\">click</span>)=<span class=\"hljs-string\">\"dynForm.tdmForm.commitToModel(true)\"</span>&gt;</span>SAVE<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">button</span> <span class=\"hljs-attr\">mat-button</span> [<span class=\"hljs-attr\">disabled</span>]=<span class=\"hljs-string\">\"dynForm.form.pristine\"</span> (<span class=\"hljs-attr\">click</span>)=<span class=\"hljs-string\">\"dynForm.tdmForm.reset()\"</span>&gt;</span>CANCEL<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">button</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">dynamic-form</span>&gt;</span>","title":"Template"},{"file":"commit.component.scss","lang":"scss","section":"default","code":"","title":"Style"},{"file":"model.ts","lang":"ts","section":"default","code":"<span class=\"hljs-keyword\">import</span> { Model, Prop } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'@tdm/ngx-dynamic-forms'</span>;\n\n<span class=\"hljs-meta\">@Model</span>({\n  form: <span class=\"hljs-literal\">true</span>\n})\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">class</span> Hero {\n  <span class=\"hljs-meta\">@Prop</span>({\n    form: {\n      render: {\n        vType: <span class=\"hljs-string\">'number'</span>,\n        label: <span class=\"hljs-string\">'Hero ID'</span>\n      }\n    }\n  })\n  id: <span class=\"hljs-built_in\">number</span>;\n\n  <span class=\"hljs-meta\">@Prop</span>({\n    form: {\n      required: <span class=\"hljs-literal\">true</span>,\n      render: {\n        vType: <span class=\"hljs-string\">'text'</span>,\n        label: <span class=\"hljs-string\">'Hero Name'</span>\n      }\n    }\n  })\n  name: <span class=\"hljs-built_in\">string</span>;\n\n  <span class=\"hljs-meta\">@Prop</span>({\n    form: {\n      render: {\n        vType: <span class=\"hljs-string\">'date'</span>,\n        label: <span class=\"hljs-string\">'Hero Birth'</span>\n      }\n    }\n  })\n  birth: <span class=\"hljs-built_in\">string</span>;\n\n  <span class=\"hljs-meta\">@Prop</span>({\n    form: {\n      render: {\n        vType: <span class=\"hljs-string\">'boolean'</span>,\n        label: <span class=\"hljs-string\">'Has Tracking Device'</span>\n      }\n    }\n  })\n  hasTracking: <span class=\"hljs-built_in\">boolean</span>;\n\n  <span class=\"hljs-meta\">@Prop</span>({\n    form: {\n      render: {\n        vType: <span class=\"hljs-string\">'slideToggle'</span>,\n        label: <span class=\"hljs-string\">'Double Agent'</span>\n      }\n    }\n  })\n  doubleAgent: <span class=\"hljs-built_in\">boolean</span>;\n\n  <span class=\"hljs-meta\">@Prop</span>({\n    form: {\n      render: {\n        vType: <span class=\"hljs-string\">'slider'</span>,\n        label: <span class=\"hljs-string\">'BMI Index'</span>,\n        data: { min: <span class=\"hljs-number\">1</span>, max: <span class=\"hljs-number\">35</span> }\n      }\n    }\n  })\n  bmi: <span class=\"hljs-built_in\">number</span>;\n\n  <span class=\"hljs-meta\">@Prop</span>({\n    form: {\n      render: {\n        vType: <span class=\"hljs-string\">'select'</span>,\n        label: <span class=\"hljs-string\">'Super Power'</span>,\n        data: {\n          options: [\n            { value: <span class=\"hljs-string\">'selfHealing'</span>, label: <span class=\"hljs-string\">'Self Healing'</span> },\n            { value: <span class=\"hljs-string\">'flying'</span>, label: <span class=\"hljs-string\">'Flying'</span> },\n            { value: <span class=\"hljs-string\">'cloaking'</span>, label: <span class=\"hljs-string\">'Cloaking'</span> },\n            { value: <span class=\"hljs-string\">'cloning'</span>, label: <span class=\"hljs-string\">'Cloning'</span> },\n            { value: <span class=\"hljs-string\">'invisibility'</span>, label: <span class=\"hljs-string\">'Invisibility'</span> }\n          ]\n        }\n      }\n    }\n  })\n  superPower: <span class=\"hljs-string\">'selfHealing'</span> | <span class=\"hljs-string\">'flying'</span> | <span class=\"hljs-string\">'cloaking'</span> | <span class=\"hljs-string\">'cloning'</span> | <span class=\"hljs-string\">'invisibility'</span>;\n\n  <span class=\"hljs-meta\">@Prop</span>({\n    form: {\n      required: <span class=\"hljs-literal\">true</span>,\n      render: {\n        vType: <span class=\"hljs-string\">'textarea'</span>,\n        label: <span class=\"hljs-string\">'Bio'</span>,\n        data: {\n          autoSize: <span class=\"hljs-literal\">false</span>,\n          minRows: <span class=\"hljs-number\">3</span>,\n          maxRows: <span class=\"hljs-number\">5</span>\n        }\n      }\n    }\n  })\n  bio: <span class=\"hljs-built_in\">string</span>;\n}\n","title":"Model"}]